<template>
  <div>
    <vue-particles
      class="login-background"
      color="#97D0F2"
      :particleOpacity="0.7"
      :particlesNumber="50"
      shapeType="circle"
      :particleSize="4"
      linesColor="#97D0F2"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.8"
      :linesDistance="150"
      :moveSpeed="3"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push">
    </vue-particles>
    <div style="width: 80%;margin: 0 auto">

      <el-row :gutter="20">
        <br>
        <el-col :span="16">
          <div class="grid-content bg-purple show">
            <br>
            <div style="margin-left: 2%">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/unityshow'}"></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/unityshow/unityQuizList'}"></el-breadcrumb-item>
                <el-breadcrumb-item ></el-breadcrumb-item>
              </el-breadcrumb>

            </div>
            <br><br>
            <div style="width: 98%;margin: 0 auto">
              <router-view></router-view>
              <br>
            </div>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple aside" style="background-color: whitesmoke">

            <el-card class="show">
              <div>
                <div style="width:30%;height: 100px;border-radius: 100px;display:inline-block;">
                  <el-image
                    style="width:100%;height: 100%;border-radius: 100px"
                    src="../../static/temporary/headpic.jpg"
                    class="image"
                  />
                </div>
                <div>
                  <p style="font-size: larger;font-family: "></p>
                  <span style="font-size: smaller">
                
                
              </span>
                </div>

                <div>
                  <p>12</p>
                  <p>24</p>
                </div>
              </div>
            </el-card>

            <el-card class="show">
              <router-link class="alink" to="/writeblog">
                <el-button  type="primary" icon="el-icon-edit">
                  
                </el-button>
              </router-link>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-button  @click="submit" type="primary" icon="el-icon-edit" >
                
              </el-button>
            </el-card>
            <br>
            <el-card class="show">
              <div class="show" style="height: 300px">
                <p>
                  <el-button icon="el-icon-s-custom" type="primary" round size="big"></el-button>
                </p>
                <div>
                  <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                  <el-avatar src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"></el-avatar>
                  <el-avatar src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"></el-avatar>
                  <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                  <el-avatar src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"></el-avatar>
                  <el-avatar src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"></el-avatar>
                  <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
                  <el-avatar src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"></el-avatar>
                  <el-avatar src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"></el-avatar>
                </div>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>

      <el-dialog
        style="width:80%;margin: 0 auto"
        title=""
        :visible.sync="dialogFormVisible"
      >

        <el-form :model="ruleForm"
                 :rules="rules"
                 ref="ruleForm"
                 label-width="100px"
                 class="demo-ruleForm">

          <el-form-item label="" prop="que" >
            <el-input v-model="ruleForm.que"></el-input>
          </el-form-item>



          <el-form-item label="" prop="desc">
            <el-input type="textarea" :rows="10" v-model="ruleForm.desc"></el-input>
          </el-form-item>
        </el-form>

        <div  style="margin: 0 auto" slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
        </div>

      </el-dialog>
    </div>
  </div>

</template>

<script>
export default {
  name: "unityshow",
  data(){
    return {
      dialogFormVisible: false,

      ruleForm: {
        que: '',
        desc: '',
      },

      rules: {
        que: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 50, message: ' 5  50 ', trigger: 'blur'}
        ],
        desc: [
          {required: true, message: '', trigger: 'blur'},
          {min: 10, max: 500, message: ' 10  500 ', trigger: 'blur'}
        ],
      }

    }
  },
  methods: {

    // 
    submit(){
      //htmlmarkdown

      this.dialogFormVisible=true;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.ruleForm.que===""){
            alert("")
            return
          }
          alert('submit!');
          this.dialogFormVisible = false;

        } else {
          return false;
        }
      });
    },

  },
}
</script>

<style scoped>
.aside{
  text-align: center;
}
.login-background {
  width: 96%;
  margin: 0 auto;
  height: 100%; /**100% */
  z-index: 0;
  position: absolute;
}
.message{
  width: 25em;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}

.alink{

  text-decoration: none;
  color: #333333;
}

.show{
  margin: 20px auto;
  width: 100%;

  border: 0px solid #81badc;
  transition: all 0.9s;
  border-radius: 10px;

}
.show:hover{
  box-shadow: 0px 15px 30px rgb(12, 132, 224);
  margin-top: 10px;

}
</style>



