<template>
<div class="showansinfo">
  <div >
      <!--      user-->
    <br>
      <div style="height:100px">
        <div  style="width:16%;height: 100%;border-radius: 100px;margin: 0 auto">
          <el-image
            style="width:100%;height: 100%;border-radius: 100px"
            src="../../static/temporary/showbg1.jpg"
            class="image"

          />
          <p style="font-family: Microsoft YaHei;
          margin: 0 auto;text-align: center">
            Huterox
          </p>
        </div>
      </div>

      <br>
      <br>
<!--    -->
      <div>
        <div style="width: 96%;margin: 0 auto">
          <p style="font-family: cursive">
          

          96cell 

          
           7 x 7 x 13 

           bounding box
        </p>
        </div>
      </div>
  </div>

  <div>
    <div style="width: 96%;margin: 0 auto">
      :
      <i class="el-icon-view"></i>
      {{Messages.number}}
      &nbsp;&nbsp;
      :
      <i class="el-icon-star-off"></i>
      {{Messages.collect}}
      &nbsp;
      :
      <i class="el-icon-check"></i>
      {{Messages.favorite}}

      <br><br>
    </div>
  </div>
  <br><br>
  <div class="show">
    <comment_ans></comment_ans>
  </div>
</div>
</template>

<script>
import comment_ans from "../../components/comment/comment_ans";
export default {
  name: "ansshowinfo",
  components: {
    comment_ans
  },
  data(){
    return{

      Messages:
        {"info":"Spring ",
          "name":"Spring 5 ",
          "number": 999,"data":"2022-3-27","favorite": 999,
          "collect":100,
        },

    }
  },
}
</script>

<style scoped>
.showansinfo{
  margin: 20px auto;
  width: 100%;

  border: 0px solid #81badc;
  transition: all 0.9s;
  border-radius: 10px;

}
.showansinfo:hover{
  box-shadow: 0px 15px 30px rgb(12, 132, 224);
  margin-top: 10px;

}
</style>



