<template>
<div style="width: 70%;margin-top: 5%">
  <el-form :model="ruleForm"
           :rules="rules"
           ref="ruleForm"
           label-width="100px"
           class="demo-dynamic"


  >
    <el-form-item
      style="width: 49%"
      label=""
      prop="name"
      :rules="[
          { required: true, message: '3~15', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  5 ', trigger: 'blur' }
        ]"

    >
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>
    <br>
    <el-form-item label="" prop="region">
      <el-select v-model="ruleForm.sex" placeholder="">
        <el-option label="" value="shanghai"></el-option>
        <el-option label="" value="beijing"></el-option>
      </el-select>
    </el-form-item>

    <el-form-item
      style="width: 49%"
      label=""
      prop="city"
      :rules="[
          { required: false, message: '3~20', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  5 ', trigger: 'blur' }
        ]"

    >
      <el-input v-model="ruleForm.city"></el-input>
    </el-form-item>

    <el-form-item
      style="width: 60%"
      prop="email"
      label=""
      :rules="[
      { required: true, message: '', trigger: 'blur' },
      { type: 'email', message: '', trigger: ['blur', 'change'] }
    ]"
    >
      <el-input v-model="ruleForm.email"></el-input>
    </el-form-item>

    <el-form-item
      style="width: 80%;height: 120px"
      label=""
      prop="motto"
      :rules="[
          { required: false, message: '', trigger: 'blur' },
          { min: 0, max: 150, message: '150', trigger: 'blur' }
        ]"

    >
      <el-input type="textarea" style="height: 100px;resize: none" v-model="ruleForm.motto"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>

    </el-form-item>
  </el-form>
</div>
</template>

<script>
export default {
  name: "infoeditor",
  data() {
    return {
      ruleForm: {
        name: '',
        sex: '',
        email: '',
        city: '',
        motto: '',

      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

  }

}
</script>

<style scoped>

</style>



