<template>
  <div style="height: 960px;width: 80%;margin: 0 auto">
    <el-row :gutter="22">
      <el-col :span="16">
        <div class="grid-content bg-purple">
          <div class="block" style="width: 100%;margin: 0 auto">

            <br><br>
            <div style="margin-left: 2%">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/quiz' }"></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/quiz/quizTui'}"></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/quiz/quizFriend'}"></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/quiz/quizLast'}"></el-breadcrumb-item>
                <el-breadcrumb-item ></el-breadcrumb-item>
              </el-breadcrumb>

            </div>
            <br>
            <div style="width: 98%;margin: 0 auto">
              <router-view></router-view>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="grid-content bg-purple" style="position: fixed" >
<!--          -->
          <el-image
            fit="fill"
            style="width:100%;height: 250px;border-radius: 5px"
            src="../../static/image/pen.jpg"
            class="image"
          />
        </div>
        <div style="position: fixed;top: 330px;left: 78%;width: 80px;height: 80px">
          <el-button style="width: 100%;height: 100%" @click="submit" type="primary" icon="el-icon-edit" circle></el-button>
        </div>

      </el-col>
    </el-row>


    <el-dialog
      style="width:80%;margin: 0 auto"
      title=""
      :visible.sync="dialogFormVisible"
    >

      <el-form :model="ruleForm"
               :rules="rules"
               ref="ruleForm"
               label-width="100px"
               class="demo-ruleForm">

        <el-form-item label="" prop="que" >
          <el-input v-model="ruleForm.que"></el-input>
        </el-form-item>



        <el-form-item label="" prop="desc">
          <el-input type="textarea" :rows="10" v-model="ruleForm.desc"></el-input>
        </el-form-item>
      </el-form>

      <div  style="margin: 0 auto" slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>

    </el-dialog>



  </div>
</template>

<script>
export default {
  name: "quiz",
  data(){
    return {
      dialogFormVisible: false,

      ruleForm: {
        que: '',
        desc: '',
      },

      rules: {
        que: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 50, message: ' 5  50 ', trigger: 'blur'}
        ],
        desc: [
          {required: true, message: '', trigger: 'blur'},
          {min: 10, max: 500, message: ' 10  500 ', trigger: 'blur'}
        ],
      }

    }
  },
  methods: {

    // 
    submit(){
      //htmlmarkdown

      this.dialogFormVisible=true;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.ruleForm.que===""){
            alert("")
            return
          }
          alert('submit!');
          this.dialogFormVisible = false;

        } else {
          return false;
        }
      });
    },

  },
}
</script>

<style scoped>



.bg-purple {
  background: white;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.message{
  width: 25em;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}



</style>



