
<template>
  <div id="main">
    <div>
      <div>
        <input type="text" v-model="form.title" placeholder="" required>
        <el-button @click="submit" type="primary"></el-button>

      </div>

      <br>
      <br>

      <mavon-editor
        v-model="form.value"
        ref="md"
        @change="change"
        style="min-height: 800px;width: 100%"
      />
    </div>

    <el-dialog
      style="width:80%;margin: 0 auto"
      title=""
      :visible.sync="dialogFormVisible"
    >

      <el-form :model="ruleForm"
               :rules="rules"
               ref="ruleForm"
               label-width="100px"
               class="demo-ruleForm">

        <el-form-item label="" >
<!--          <el-input v-model="ruleForm.url"></el-input>-->
          <signle-upload v-model="ruleForm.url"></signle-upload>
        </el-form-item>

        <el-form-item label="" prop="community">
          <el-select v-model="ruleForm.community" placeholder="">
            <el-option label="" value="A"></el-option>
            <el-option label="" value="B"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="community">
          <el-select v-model="ruleForm.column" placeholder="">
            <el-option label="java" value="java"></el-option>
            <el-option label="python" value="python"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="level">
          <el-select v-model="ruleForm.level" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="type">
          <el-radio-group v-model="ruleForm.type">
            <el-radio label="" value="0"></el-radio>
            <el-radio label="" value="1"></el-radio>
            <el-radio label="" value="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
      </el-form>

      <div  style="margin: 0 auto" slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>

    </el-dialog>


  </div>



</template>

<script>
import signleUpload from "../../components/upload/signleUpload";
import { mavonEditor } from 'mavon-editor'     //mavon-editor
import 'mavon-editor/dist/css/index.css'       //
export default {
  // 
  name: "writeblog",
  components: {
    mavonEditor,
    signleUpload,
  },

  data() {
    return {

      dialogFormVisible: false,
      form: {
        value:'', // markdown
        html:'',    // html
        ruleForm:this.ruleForm,
        title: '',


      },
      ruleForm: {
        url: '',
        community:"",
        column: '',
        level:'',
        desc: '',
        type:'',
      },

      rules: {
        desc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 150, message: ' 1  150 ', trigger: 'blur' }
        ],

        community: [
          { required: true, message: '', trigger: 'blur' },
        ],

        level:[
          { required: true, message: '', trigger: 'blur' },
        ],

        type: [
          { required: true, message: '', trigger: 'change' }
        ],

      }
    }
  },

  methods: {
    // 
    change(value, render){        //valuemarkdownrenderhtml
      this.form.html = render;
    },
    // 
    submit(){
      //htmlmarkdown
      console.log(this.form.value);
      console.log(this.form.html);
      this.dialogFormVisible=true;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.form.title===""){
            alert("")
            return
          }
          alert('submit!');
          this.dialogFormVisible = false;
          console.log(this.ruleForm)
        } else {

          console.log('error submit!!');
          return false;
        }
      });
    },

  },
  mounted() {

  }


}
</script>
<style scoped>
#center {
  margin-top: 5%;
  width: 96%;
  height: 96%;
  border: 1px;
}

img {
  margin: auto;
  margin-left: 30%;
  height: 40%;
  width: 40%;
  position: relative;
  top: 10%;
}

input {

  width: 85%;
  height: 30px;
  border-width: 2px;
  border-radius: 5px;
  border-color: #00c4ff;
  border-bottom-color: #2C7EEA;
  color: #586e75;
  font-size: 15px;

}

</style>






